* {
  box-sizing: border-box;
  text-rendering: geometricprecision;
  margin: 0;
  padding: 0;
}

:root {
  --padding: 18px;
}

main {
  margin: 0 10vh;
}

body {
  background-color: #f3f3f3;
}

section {
  margin: 0 auto;
  font-family: Helvetica, sans-serif;
}

.example {
  justify-content: flex-start;
  margin-top: -40px;
  margin-left: 26px;
  display: flex;
}

.wrapper {
  background-color: #fff;
  border-radius: 8px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 5em;
  padding: 2em;
  display: flex;
  box-shadow: 0 2px 4px #0000002e;
}

h1 {
  padding: 0 var(--padding);
  text-align: left;
  color: #1d1d1d;
  margin: .7em 0 .5em;
  font-size: 1.6em;
}

h2 {
  text-align: left;
  z-index: 2;
  width: 95%;
  color: #1d1d1d;
  margin-top: 1em;
  margin-bottom: -1em;
  padding: .5em 1.6em;
  font-size: 1.4em;
}

p {
  padding: .5em var(--padding);
  color: #171717;
  font-size: 1.1em;
  line-height: 1.4;
}

img {
  width: 90%;
  height: 90%;
  object-fit: contain;
}

a {
  color: #000;
  text-decoration: none;
}

a:hover {
  color: #e17373;
}

.ingress {
  margin-bottom: 3vh;
  padding: 2em;
}

.button-wrapper {
  padding: .5em var(--padding);
  margin-left: .15em;
}

button {
  color: #fff;
  letter-spacing: 1.1px;
  background-color: #252525;
  border: none;
  margin: 1.2em .6em 0 0;
  padding: .5em 1em;
  font-family: Helvetica, sans-serif;
  font-size: 1em;
}

@media (max-width: 768px) {
  .wrapper {
    padding: 0;
  }

  .ingress {
    padding: 1em;
  }

  h1 {
    margin: 1em 0;
    font-size: 1.4em;
  }

  h2 {
    margin-top: 1.2em;
    font-size: .8em;
  }

  p, a {
    font-size: .9em;
  }

  .wrapper {
    margin-top: 2em;
  }

  main {
    margin: 3vw;
  }
}

